.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 250px;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
  width: 700px;
  margin: 0 auto;
}

.dark-mode .header {
  background-color: #333;
  color: #fff;
}

.nav {
  transform: translateX(150px);
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  align-items: center;
  column-gap: 5rem;
}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav__list {
  display: flex;
  column-gap: 2rem;
  margin-left: auto;
  justify-content: space-between;
}


.nav__img {
  width: 50px;
  margin-right: auto;
}


.nav__link {
  font-family: 'Inter', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: 0.3s;
  margin-top: 2.28rem;
}

.nav__item {
  margin-left: auto;
}

.nav__darkmode {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 34rem;
}

.nav__icon,
.nav__close,
.nav__toggle {
  display: none;
}

.dark-mode .nav__logo,
.dark-mode .nav__toggle,
.dark-mode .nav__link,
.dark-mode .nav__darkmode,
.dark-mode .active-link,
.dark-mode .footer {
  color: #ffffff;
}

.active-link,
.nav__link:hover {
  color: var(--title-color-dark);
}

/* breakpoints */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
  }

  .nav {
    height: var(--header-height);
  }

  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }

  /* Show Menu */
  .show-menu {
    bottom: 0;
  }

  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }

  .nav__icon {
    font-size: 1.2rem;
  }

  .nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }

  .nav__close:hover {
    color: var(--title-color-dark);
  }

  .nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: block;
  }

  .dark-mode .nav__logo,
  .dark-mode .nav__toggle,
  .dark-mode .nav__link,
  .dark-mode .active-link {
    color: #000000;
  }
}

@media screen and (max-width: 350px) {
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }

  .nav__list {
    column-gap: 0;
  }
}
